// header

.header {
    background: linear-gradient(179.84deg, #232529 1.41%, #222529 6.93%);
    padding: 20px 0 0px 0;

    &__container {
        max-width: 1584px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        width: 68px;
    }

    &__left {
        display: flex;
        align-items: center;
        gap: 28px;
    }

    &__right {
        .btn--dashed {
            display: none;
        }
    }

    &__info {
        display: none;
        span {
            font-weight: 400;
            font-size: 14px;
            line-height: 140%;
            color: var(--light-color);

            &:not(:first-child) {
                margin-left: 28px;
            }
        }
    }

    &__wrap {
        position: absolute;
        top: 56px;
        left: 0%;
        z-index: 100;
        background-color: #23262A;
        padding: 33px 20px;
        height: calc(100vh - 56px);
        width: 100%;
        overflow-y: auto;
        transition: var(--base-transition);
        visibility: hidden;
        opacity: 0;

        &.menu--active {
            visibility: visible;
            opacity: 1;
    
        }
    }

    &__mobile {
        .btn--dashed {
            display: inline-flex;
        }
        &-wrap {
            margin-bottom: 24px;
            span {
               &:first-child {
                display: block;
                font-weight: 400;
                font-size: 14px;
                line-height: 140%;
                color: var(--light-color);
                opacity: 0.4;
                margin-bottom: 5px;
               }

               &:last-child {
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
                color: var(--light-color);
                opacity: 1;
               }
            }

            a {
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
                color: var(--light-color);
            }
        }
    }

}

.nav {

    margin-bottom: 51px;
    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        
    }

    &__link {
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: var(--light-color);
    }
}

.burger {
    --burger-width: 30px;
    --burger-height: 24px;
    --burger-line-height: 3px;
  
    position: relative;
    border: none;
    padding: 0;
    width: var(--burger-width);
    height: var(--burger-height);
    color: var(--light-color);
    background-color: transparent;
    cursor: pointer;
  
    &::before,
    &::after {
      content: "";
      position: absolute;
      right: 0;
      width: 100%;
      height: var(--burger-line-height);
      background-color: currentColor;
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
        width: 70%;
    }
  
    &::before {
      top: 0;
    }
  
    &::after {
      top: calc(100% - var(--burger-line-height));
    }
  
    &__line {
      position: absolute;
      right: 0;
      top: 50%;
      width: 50%;
      height: var(--burger-line-height);
      background-color: currentColor;
      transform: translateY(-50%);
      transition: transform 0.3s ease-in-out;
    }
  
    &--active {
      &::before {
        top: 50%;
        transform: rotate(45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }
  
      &::after {
        top: 50%;
        width: 100%;
        transform: rotate(-45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }
  
      .burger__line {
        transform: scale(0);
        transition: transform 0.3s ease-in-out;
      }
    }
}


